import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Email from "@material-ui/icons/Email";
import People from "@material-ui/icons/People";
import Tel from "@material-ui/icons/Phone";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import CheckBoxIcon from "@material-ui/icons/CheckBox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";

// import Lector Readings
import LectorReadings from "../../../components/LectorReadings";

// import Lector css
import "./Lector.css";

//import material-ui icons
import InstagramIcon from "@material-ui/icons/Instagram";
import FacebookIcon from "@material-ui/icons/Facebook";
import TwitterIcon from "@material-ui/icons/Twitter";

import styles from "assets/jss/material-kit-react/views/loginPage.js";

import image from "assets/img/bg7.jpg";
import Paper from "@material-ui/core/Paper/Paper";
const useStyles = makeStyles(styles);

export default function Lector(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  const [state, setState] = React.useState({
    checkedA: false,
    checkedB: false,
    checkedC: false
  ,
  });

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };
  setTimeout(function () {
    setCardAnimation("");
  }, 700);
  const classes = useStyles();
  const { ...rest } = props;
  return (
    <div>
      <Header
        absolute
        color="transparent"
        brand="St. Michael's Parish"
        rightLinks={<HeaderLinks />}
        {...rest}
      />
      <div
        className={classes.pageHeader}
        style={{
          backgroundImage: "url(" + image + ")",
          backgroundSize: "cover",
          backgroundPosition: "top center",
        }}
      >
        <div className={classes.container}>
          <GridContainer>
            <GridItem xs={12} sm={9}>
              <Paper className="lector__law">
                <h3>Proclaiming the Word of God</h3>
                The General Instruction of the Roman Missal states that “in the
                absence of an instituted lector, lay men and women may be
                deputed to take on this function. They should be “truly
                suited…and carefully prepared” to proclaim the Word of God to
                the gathered assembly. (GIRM, 101) From the USCCB: “Readers are
                encouraged to continually meditate on Sacred Scripture so that
                their faithful proclamation of the readings at Mass will in turn
                inspire the faithful to turn to God through His Word.
              </Paper>
              <Paper style={{ padding: 20 }}>
                <LectorReadings />
              </Paper>
            </GridItem>

            <GridItem xs={12} sm={3}>
              <Card className={classes[cardAnimaton]}>
                <form className={classes.form}>
                  <h4 style={{ padding: 10, paddingRight: 10, textAlign:"center" }}>
                    Like to Learn More About Lectoring
                  </h4>
                  <p style={{ paddingLeft: 10, paddingRight: 10 }}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={state.checkedA}
                          onChange={handleChange}
                          name="checkedA"
                          color="primary"
                        />
                      }
                      label="Lectoring during weekend Masses"
                    />
                  </p>
                  <p style={{ padding: 10, paddingRight: 10 }}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={state.checkedB}
                          onChange={handleChange}
                          name="checkedB"
                          color="primary"
                        />
                      }
                      label="Lectoring during weekday Masses"
                    />
                  </p>
                  <p style={{ padding: 10, paddingRight: 10 }}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={state.checkedC}
                          onChange={handleChange}
                          name="checkedC"
                          color="primary"
                        />
                      }
                      label="General information about being a lector"
                    />
                  </p>
                  <CardBody>
                    <CustomInput
                      labelText="First Name..."
                      id="first"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        type: "text",
                        endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ),
                      }}
                    />
                    <CustomInput
                      labelText="Tel..."
                      id="tel"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        type: "tel",
                        endAdornment: (
                          <InputAdornment position="end">
                            <Tel className={classes.inputIconsColor} />
                          </InputAdornment>
                        ),
                      }}
                    />
                    <CustomInput
                      labelText="Email..."
                      id="email"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        type: "email",
                        endAdornment: (
                          <InputAdornment position="end">
                            <Email className={classes.inputIconsColor} />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </CardBody>
                  <CardFooter className={classes.cardFooter}>
                    <Button simple color="primary" size="lg">
                      Submit
                    </Button>
                  </CardFooter>
                </form>
              </Card>
              <Paper>
                <CardHeader color="primary" className={classes.cardHeader}>
                  <h4>MINISTRY CONTACTS:</h4>
                </CardHeader>
                <div style={{ padding: 10, textAlign: "center" }}>
                  <p> For Sunday Masses:</p>
                  <b>Mr. Peprah</b>
                  <p>Tel: 0506765895</p>
                </div>
              </Paper>
            </GridItem>
          </GridContainer>
        </div>
        <Footer whiteFont />
      </div>
    </div>
  );
}
